import React from 'react';
import { useNavigate } from 'react-router-dom';
import HomeComponent from './HomeComponent';
import { useStoreon } from 'storeon/react';
import BlockSpinner from '../../Views/SpinnerWrapper';
import './Home.css';

const Home = ({
  context
}) => {
  const { currency, inStockStore } = useStoreon('currency', 'inStockStore');

  const in_stock_product_filters = context?.main_page?.in_stock_product_filters;
  const first_screen = context?.main_page?.first_screen;
  const { banners, profile, partner_banners, news, about_banner, reviews } = context;
  const { page_type_catalog, page_type_news, page_type_reviews, main_title_video } = context.site_configuration;

  const navigate = useNavigate();

  const goToCatalog = (path) => {
    navigate(path)
  }

  return (
    <React.Fragment>
      {
        first_screen ?
          <React.Fragment>
            <HomeComponent.TradingPlatform
              first_screen={first_screen}
              page_type_catalog={page_type_catalog}
              goToCatalog={goToCatalog}
              main_title_video={main_title_video}
            // front_admin = {props.profile.front_admin}
            />

            <HomeComponent.MainCategories
              banners={banners}
            //   front_admin = {props.profile.front_admin}
            />


            {
              inStockStore ?
                !!inStockStore?.results?.length ?
                  <HomeComponent.ProductsInStock
                    role={profile.role}
                    // front_admin = {props.profile?.front_admin}
                    in_stock_product_filters={in_stock_product_filters}
                    products={inStockStore?.results}
                    isLoading={inStockStore.isLoading}
                    catalog_url={page_type_catalog}
                    currency={currency}
                  />
                  : null
                : null
            }


            <HomeComponent.Cooperation
              // front_admin = {props.profile.front_admin}
              partner_banners={partner_banners}
            />

            <HomeComponent.MainNews
              // front_admin = {props.profile.front_admin}
              news={news}
              news_url={page_type_news}
            />

            <HomeComponent.MainAbout
              // front_admin = {props.profile.front_admin}
              about_banner={about_banner}
            />

            {/*
                  <HomeComponent.LivePhotos
                    //Живые фото
                    live_photos={live_photos}
                    live_photos_url={page_type_live_photos}
                  /> 
            */}

            <HomeComponent.MainReviews
              // front_admin = {props.profile.front_admin}
              reviews_url={page_type_reviews}
              product_reviews={reviews.product_reviews}
              service_reviews={reviews.service_reviews}
              profileId={profile.id}
            />
          </React.Fragment>

          :  <BlockSpinner.SpinnerWrapper>
          <BlockSpinner.SpinnerCenter>
              <BlockSpinner.Spinner sizeWidth={30} sizeHeight={30}/>
          </BlockSpinner.SpinnerCenter>
      </BlockSpinner.SpinnerWrapper>
      }

    </React.Fragment>
  );
}

export default (Home);
